import React from 'react';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import 'bootstrap/dist/css/bootstrap.min.css';

import Button from 'react-bootstrap/Button';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';

import About from "./About";
import Tutorial from "./Tutorial";


import test from './res/slam_ico.svg'

function App() {
  return <div>

    <Router>
      <Navbar bg="light" expand="lg">
          <Navbar.Brand href="#home"><img src = {test} style = {{width: 40}}/></Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
              <Nav.Link href="/">About</Nav.Link>
              <Nav.Link href="/tutorial">Tutorials</Nav.Link>
              <Nav.Link href="#doc">Documentation</Nav.Link>
          </Nav>
          </Navbar.Collapse>
          <Button style = {{borderColor: `white`, backgroundColor: `rgba(0, 0, 0, 0.8)`}} variant="primary">Join us</Button>
      </Navbar>

      <Switch>
        <Route path="/tutorials/slam_and_nav">
          <Tutorial />
        </Route>
        <Route path="/">
          <About />
        </Route>
      </Switch>
    </Router>
  </div>
}

export default App;