import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

import Button from 'react-bootstrap/Button';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Jumbotron from 'react-bootstrap/Jumbotron';
import Carousel from 'react-bootstrap/Carousel'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

function About() {
  return <div style = {{backgroundPosition: '50% 60%', backgroundSize: 'cover', background: "#161616", width: 'auto', height: '900px'}}>
    <Jumbotron
      style = {
        {
          borderRadius: 0,
          backgroundPosition: '0% 2%',
          backgroundImage: 'url(https://upload.wikimedia.org/wikipedia/commons/b/bd/Ouster_OS1-64_lidar_point_cloud_of_intersection_of_Folsom_and_Dore_St%2C_San_Francisco.png)',
          maxWidth: '100%',
          height: '350px',
        }
      }
    > 
      <h1 style = {{color: `white`}}>What is SLAM and Navigation?</h1>
      <p style = {{color: `white`, fontSize: '18px'}}>
        <b>Simultaneous localization and mapping</b> and <b>navigation</b> refer to two independent but related processes:
        <ul>
         <li>constructing a map while keeping track of your location within it</li>
          <li>finding an optimal path between your current location and a destination on that map</li>
       </ul>
      </p>
       <p>
        <Button href = "/tutorials/slam_and_nav" style = {{marginTop: '20px', borderColor: `white`, backgroundColor: `rgba(0, 0, 0, 0.4)`}} variant="primary">Learn more</Button>
      </p>
      <p style = {{textAlign: `right`}}>
        <a style = {{color: `white`}} href="https://commons.wikimedia.org/wiki/File:Ouster_OS1-64_lidar_point_cloud_of_intersection_of_Folsom_and_Dore_St,_San_Francisco.png" title="via Wikimedia Commons">Daniel L. Lu</a> <a style = {{color: `white`}} href="https://creativecommons.org/licenses/by/4.0">[CC BY 4.0]</a>
      </p>
    </Jumbotron>
    
      <Row style = {{width: '100%', margin: '0 0 0 0'}}>
        <Col xs = {3} style = {{margin: '0 0 0 0', padding: '0 0 0 0'}}>

          <Jumbotron
            style = {
              {
                borderRadius: '0 10 0 10',
                backgroundPosition: '25% 2%',
                background: "black",
                maxWidth: '90%',
                height: '400px',
              }
            }
          > 
            <p style = {{color: `white`, fontSize: '18px'}}>Featured Project</p>
            <h1 style = {{color: `white`}}>Caffeine</h1>
            <p style = {{color: `white`, fontSize: '18px'}}>
              We are currently working on a robot, called Caffeine, for the 2020 IGVC competition.
            </p>
            <p>
              <Button href = "https://github.com/UTRA-ART/SLAM" style = {{marginTop: '20px', borderColor: `white`, backgroundColor: `rgba(0, 0, 0, 0.4)`}} variant="primary">GitHub Repository</Button>
            </p>
          </Jumbotron>

        </Col>

        <Col style = {{margin: '0 0 0 0', padding: '0 0 0 0'}}>

          <Carousel style = {{borderRadius: '10 0 10 0'}}>
            <Carousel.Item>
              <div style = {{background: 'black', width: '100%', height: '400px'}}></div>
              <Carousel.Caption>
                <h1>Meet the Team</h1>
                <p style = {{fontSize: '18px'}}>The SLAM and Navigation division of the Autonomous Rover Team is a small but dedicated group of students working towards creating fully autonomous robots.</p>
                
              </Carousel.Caption>
            </Carousel.Item>
          </Carousel>

        </Col>
      </Row>
  </div>
}

export default About;