import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

import Breadcrumb from 'react-bootstrap/Breadcrumb'

import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Tabs from 'react-bootstrap/Tabs'
import Tab from 'react-bootstrap/Tab'

import './App.css'

import logo from './res/ra.png';

function Tutorial() {

  return <div style = {{backgroundSize: 'full', backgroundColor: '#f2f2f2', backgroundImage: 'url(' + logo + ')', width: '100%', minHeight: '655px'}}>
    <div 
      style = {{backgroundPosition: '2% 5%', width: 'auto', height: '80px', backgroundImage: 'url(https://upload.wikimedia.org/wikipedia/commons/b/bd/Ouster_OS1-64_lidar_point_cloud_of_intersection_of_Folsom_and_Dore_St%2C_San_Francisco.png)'}}>
    </div>
    
    <Row style = {{width: '100%', margin: '0 0 0 0'}}>
        <Col xs = {8} style = {{margin: '0px 0px 0px 20px', padding: '0 0 0 0'}}>
          <div style = {{margin: '25px 0px 0px 0px', padding: '0px 0px 20px 0px'}}>
          <div 
            className = 'scroll scroll4'>
            <h2 style = {{margin: '10px 0 20px 0px', padding: '0 0 0 0 ', color: '#050505'}}>What is SLAM and Navigation?</h2>
            <b>Simultaneous localization and mapping</b> and <b>navigation</b> refer to two (mostly) independent but related processes:
            <br></br>
            <br></br>
            <ul>
              <li>constructing a map while keeping track of your location within it</li>
              <li>finding an optimal path between your current location and a destination on that map</li>
            </ul>
            Since these processes are independent (again mostly), we can focus on them in isolation.
            <h3 style = {{margin: '10px 0 20px 0px', padding: '0 0 0 0 ', color: '#050505'}}>Simultaenous Localization and Mapping</h3>
            <p style = {{color: '#050505'}}>
              <b>Simultaenous localization and mapping</b>, or SLAM, is really two problems in and of itself:
              <br></br>
              <br></br>
              <ol>
                <li>Assuming we have an accurate map of the environment, we need a way to determine our relative location within it, i.e., <b>localization</b>.</li>
                <li>Assuming we can accuraely determine changes to our location, we need a way to build that map of the environment, i.e., <b>mapping</b>.</li>
              </ol>
              
              Let's tackle these two problems seperately first.
            </p>
            <h4 style = {{margin: '10px 0 20px 0px', padding: '0 0 0 0 ', color: '#050505'}}>Localization</h4>
            <p style = {{color: '#050505'}}>
              Suppose $x$ is our (unknown) location on the map. Typically, $x$ would be a vector in either $\mathbb R ^2$ or $\mathbb R^3$, depending on the dimensionality of the environment.
              Let's keep things simple and assume $x \in \mathbb R^2$.
            </p>
          </div>
          </div>
        </Col>
        <Col style = {{margin: '0 0 0 0', padding: '0 0 0 0'}}>
          <div style = {{margin: '25px 0px 0px 0px', padding: '0px 0px 20px 0px'}}>
          <div 
            className = 'scroll scroll4'>
            <Tabs defaultActiveKey="profile" id="uncontrolled-tab-example">
  <Tab eventKey="home" title="Visualization">
  </Tab>
  <Tab eventKey="profile" title="Data">
  </Tab>
</Tabs>
          </div>
          </div>
        </Col>
      </Row>
  </div>
}

export default Tutorial;